        ÊÇ®ÁöÑÊµèËßàÂô®‰∏çÊîØÊåÅÈü≥È¢ëÊí≠Êîæ
      </audio>
      <button @click="toggleMusic" class="music-btn">
        {{ isPlaying ? 'üéµ ÊöÇÂÅúÈü≥‰πê' : 'üéµ Êí≠ÊîæÈü≥‰πê' }}
      </button>
    </div>
    <!-- ‰∏ªÂÜÖÂÆπ -->
