    },
    initializeAudio() {
      this.toggleMusic()
      document.removeEventListener('click', this.initializeAudio)
    }
  },
  mounted() {
    // Ëá™Âä®Êí≠ÊîæÂ§ÑÁêÜÔºàÈúÄË¶ÅÁî®Êà∑‰∫§‰∫íÔºâ
